.form__mark {
  position: absolute;
  bottom: -10px;
  left: 4px;
  width: 46px;
  height: 46px;
  border: none;
  background-color: #f2f2f2;
  border-radius: 4px;
}
.form__alternative-input[type="checkbox"] + .form__mark:focus {
  outline: 2px solid #0b5ba3;
}
.form__alternative-input[type="checkbox"] + .form__mark:hover {
  background-color: #e5e5e5;
}
.form__alternative-input[type="checkbox"]:checked + .form__mark::before {
  position: absolute;
  background-image: url("../img/form-checked.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  content: "";
  top: 14px;
  left: 12px;
  width: 22px;
  height: 16px;
}
.form__alternative-input[type="checkbox"]:checked + .form__mark::before:active {
  background-image: url("../img/checked-active.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.form__alternative-input[type="radio"] + .form__mark {
  border-radius: 50%;
}
.form__alternative-input[type="radio"] + .form__mark:focus {
  outline: 2px solid #0b5ba3;
}
.form__alternative-input[type="radio"] + .form__mark:hover {
  background-color: #e5e5e5;
}
.form__alternative-input[type="radio"] + .form__mark:active {
  opacity: 30%;
  outline: none;
}
.form__alternative-input[type="radio"]:checked + .form__mark::before {
  position: absolute;
  background-color: #333333;
  border-radius: 50%;
  content: "";
  width: 16px;
  height: 16px;
  left: 15px;
  top: 15px;
}
.form__alternative-input[type="radio"]:checked + .form__mark::after {
  position: absolute;
  top: -2px;
  left: -1px;
  width: 46px;
  height: 46px;
  content: "";
}
